import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const OFF = 0.07;

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing(1),
    overflowX: 'auto',
  },
  table: {
    minWidth: 80,
  },
}));

function ccyFormat(num) {
  return `${num.toFixed(3)}`;
}

function priceRow(qty, unit) {
  return qty * unit;
}

function createRow(date, status, price) {  
  return { date, status, price };
}

function subtotal(items) {
  return items.map(({ price }) => price).reduce((sum, i) => sum + i, 0);
  //return items.map(({status, price }) => {status, price }).reduce((sum, i) =>  (status == 'تائید') ? (sum + i): 0);
}

const rows = [
  createRow('1398/01/01', 'تائید', 150.200),
  createRow('1398/01/02', 'تائید', 450.0),
  createRow('1398/01/03', 'تائید', 170.000),
];

const invoiceSubtotal = subtotal(rows);
const invoiceOff = OFF * invoiceSubtotal;
const invoiceTotal = invoiceSubtotal - invoiceOff;

export default function SpanningTable() {
  const classes = useStyles();

  return (
    
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell>ناریخ</TableCell>            
            <TableCell align="right">وضیعت</TableCell>
            <TableCell align="right">قیمت</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow key={row.desc}>
              <TableCell>{row.date}</TableCell>
              <TableCell align="right">{row.status}</TableCell>              
              <TableCell align="right">{ccyFormat(row.price)}</TableCell>
            </TableRow>
          ))}

          <TableRow>
            <TableCell rowSpan={3} />
            <TableCell colSpan={1}>مجموع هزینه</TableCell>
            <TableCell align="right">{ccyFormat(invoiceSubtotal)}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell>تخفیف</TableCell>            
            <TableCell align="right">{ccyFormat(invoiceOff)}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell colSpan={1}>مجموع با تخفیف</TableCell>
            <TableCell align="right">{ccyFormat(invoiceTotal)}</TableCell>
          </TableRow>
        </TableBody>
      </Table>
    
  );
}
